import React, { useState, useEffect } from "react";
import setInputValue from "./utils/SetInputValue";
import useVisualMode from "./utils/UseVisualMode";
import Welcome from "./Welcome"
import DisplayAnswer from "./CompletionScreen"
import QuizQuestion from "./QuizQuestion"
import styles from './ProductSelectorQuiz.module.scss'
import { Questions } from "./Questions"
//Atoms
import Button from '../Button';

const ProductSelector = () => {
  const [state, setState] = useState({
    question1: "",
    question2: "",
    question3: "",
    question4: "",
    question5: "",
    question6: "",
    question7: "",
    inputAdded: false
  });
  const { mode, transition } = useVisualMode(0);
  const [recommendation, setRecommendation] = useState("easystart"); //To be used for Completion screen when we have ability to use pricing card
  const [recommendedId, setRecommendedId] = useState("quizeasystartcard")


  //Next Page
  const nextPage = (event) => {
    event.preventDefault();
    setState((prev) => ({
      ...prev,
      inputAdded: true
    }));
  };
  // Previous Page
  const prevPage = (event) => {
    event.preventDefault();
    if (mode === 1) {
      transition(mode - 1);
      setState({
        question1: "",
        question2: "",
        question3: "",
        question4: "",
        question5: "",
        question6: "",
        question7: "",
        inputAdded: false
      });
    }
    transition(mode - 1);
  };

  //Check the answer and add recomendation
  const showAnswer = () => {
    if (state.question7 === "Yes") {
      setRecommendation("plus");
      setRecommendedId('quizpluscard')
    } else {
      if (state.question5 === "Yes" || state.question6 === "Yes") {
        setRecommendation("essentials");
        setRecommendedId('quizessentialscard')
      } else {
        setRecommendation("easystart");
        setRecommendedId('quizeasystartcard')
      }
    }
    if (
      (state.question1 === "No" &&
        state.question2 === "No" &&
        state.question3 === "No" &&
        state.question4 === "No" &&
        state.question5 === "No" &&
        state.question6 === "No" &&
        state.question7 === "No") ||
      (state.question1 === "na" &&
        state.question2 === "na" &&
        state.question3 === "na" &&
        state.question4 === "na" &&
        state.question7 === "na")
    ) {
      setRecommendation("selfemployed");
      setRecommendedId('quizselfemployedcard')
    }
  };

  const scrollToTop = () => {
    document.querySelector("body").scrollIntoView({
      behavior: "smooth",
    })
  }

  const reset = () => {
    //recommend flag
    document.getElementById('quizpricingcardcontainer').style.height = '0px';
    document.getElementById('quizpricingcardcontainer').style.visibility = 'hidden';
    const recommendedFlag = document.getElementById(recommendedId);
    const firstChild = recommendedFlag.firstElementChild
    if (firstChild.textContent.toLowerCase() === "recommended" && firstChild.classList.contains(styles.showRecommended)) {
      firstChild.classList.remove(styles.showRecommended);
      firstChild.classList.add("hideRecommended");
    }

    //recommend flag
    transition(mode - Questions.length);
    setState({
      question1: "",
      question2: "",
      question3: "",
      question4: "",
      question5: "",
      question6: "",
      question7: "",
      inputAdded: false
    });
    scrollToTop()
  }

  //Use Effect to check if added
  useEffect(() => {
    if (state.inputAdded === true) {
      transition(mode + 1);
      setState((prev) => ({
        ...prev,
        inputAdded: false
      }));

      if (mode === Questions.length) {
        showAnswer();
      }
    } else {
      // if they haven't, just set inputAdded to false - this is just to reset state
      setState((prev) => ({
        ...prev,
        inputAdded: false
      }));
    }
  }, [state.inputAdded]);

  //Quiz Content
  for (let i = mode; i <= Questions.length + 1; i++) {
    //Start
    if (i === 0) {
      return (
        <div data-tracking="product_selector_welcome" className={styles.productSelectorQuiz}>
          <Welcome />
          <Button
            variant="primary"
            data-wa-link="product-selector-ca-get-started"
            data-action="product-selector-ca-get-started"
            onClick={() => {
              transition(mode + 1);
            }}>Get Started</Button>
        </div>
      );
    }
    //After Completion Show this
    else if (i === Questions.length + 1) {
      return (
        <div data-tracking="product_selector" className={styles.productSelectorQuiz}>
          <DisplayAnswer recommendation={recommendation} />
        </div>
      )
    }
    //Quiz Steps
    return (
      <>
        {mode === i && (
          <div data-tracking="product_selector_question" className={styles.productSelectorQuiz}>
            <QuizQuestion
              question={Questions[i - 1]}
              state={state}
              setState={setState}
              setInputValue={setInputValue}
              prevPage={prevPage}
              nextPage={nextPage}
            />

          </div>
        )}
        <div className={styles.dots}>
          {Questions.map((dots, index) => {
            return (
              <div className={`${styles.dot} ${i === index + 1 && styles.activeDot}`} key={index}></div>
            )
          })}
        </div>
      </>
    );
  }
}

export default ProductSelector
