const initState = {
  features: {
    pricing: false,
    inventory: false,
    hosting: false
  },
  total: 0,
  users: 5,
  costing: costData[0],
}

const store = createContext()

const { Provider } = store

const StateProvider = ({ children }) => {
  const [state, dispatch] = useReducer((state, action) => {
    const { type, payload } = action

    switch (type) {
      case "setInventory":
        return { ...state, features: { ...state.features, inventory: payload } }
      case "setPricing":
        return { ...state, features: { ...state.features, pricing: payload } }
      case "setHosting":
        return { ...state, features: { ...state.features, hosting: payload } }
      case "setTotal":
        return { ...state, total: payload }
      case "setUsers":
        return { ...state, users: payload }
      case "setCosting":
        return { ...state, costing: payload }
    }
  }, initState)

  return <Provider value={{ state, dispatch }}>{children}</Provider>
}

const EnterprisePackageItem = ({ cost, name, title, features, ctas }) => {

  const { state, dispatch } = useContext(store)
  const setType = `set${name.charAt(0).toUpperCase() + name.slice(1)}`

  const handleChange = event => {
    dispatch({ type: setType, payload: event.currentTarget.checked })
  }

  return (
    <div className={styles.chartitem}>
      <div className={styles.chartitemtop}>
        <div className={styles.chartitemtitle}>{title}</div>
        <div className={styles.charttoggle}>
          <input
            data-testid={`${name}-toggle`}
            className={styles.charttoggleinput}
            checked={state.features[name]}
            onChange={event => handleChange(event)}
            id={`${name}-toggle`}
            name={name}
            type="checkbox"
            value={name}
            disabled={state.costing.features[name].state === true}
          />
          <label
            className={styles.charttogglelabel}
            htmlFor={`${name}-toggle`}
          ></label>
        </div>
      </div>

      <div className={styles.chartpriceadd}>Add</div>
      <div className={styles.chartprice}>
        <div className={styles.chartpricedollars}>${cost}</div>
        <div className={styles.chartpricecents}>00</div>
        <div className={styles.chartpriceterm}>/mo.</div>
      </div>
      {/* <ul className='chart-features checkmark__list'> */}
      <ul className={styles.chartfeatures}>
        {features.map((feature, index) => (
          <li key={index} className={styles.chartfeaturesitem}>
            <span className={styles.checkmark}>
              <img src="https://quickbooks.intuit.com/oidam/intuit/sbseg/en_ca/shared/icons/checkmark-thick.svg" alt="" /></span> {feature}
          </li>
        ))}
      </ul>
      {ctas && ctas.map((cta, index) => (
        <div key={index} className={styles.chartbtn}><Button data-wa-link={`learn-more-cta-enterprise-calculator-${name}`} variant="primary" href={cta.url}>{cta.text}</Button></div>
      ))}
    </div>
  )
}

const EnterprisePackageTotal = ({ base }) => {

  const globalState = useContext(store)
  const { state, dispatch } = globalState

  const features = featuresData

  const featuresState = state.features
  const featuresCost = state.costing.features

  const getFeatureTitle = (name) => {
    const filtered = features.filter(feature => feature.name === name)

    return filtered[0].title
  }

  const getFeatureList = () => {
    let features = []
    for (const feature in featuresState) {
      if (featuresState[feature] === true) {
        let featureObj = {}
        featureObj.text = getFeatureTitle(feature)
        featureObj.cost = featuresCost[feature].cost

        features.push(featureObj)
      }
    }
    return features
  }

  const featureList = getFeatureList()

  const reducer = (accumulator, currentValue) => accumulator + currentValue.cost

  const total = featureList.reduce(reducer, state.costing.cost)

  return (
    <div className={styles.total}>
      <div className={styles.totaltop}>
        <div className={styles.totaltitle}>Total</div>
        <div className={styles.totalprice}>
          <div data-testid="total" className={styles.totalpricedollars}>${total}</div>
          <div className={styles.totalpricecents}>00</div>
          <div className={styles.totalpriceterm}>/mo.</div>
        </div>
        <div className={styles.totalpriceinfo}>
          Annual subscription billed monthly
       </div>
      </div>
      <div className={styles.includes}>
        <div className={styles.includestitle}>Your subscription includes:</div>
        <div className={styles.includesitem}>
          <div>Desktop Enterprise Subscription</div>
          <div className={styles.includescost}>${base}</div>
        </div>
        {(featureList === undefined || featureList.length !== 0) &&
          featureList.map((feature, index) => (
            <div key={index} className={styles.includesitem}>
              <div>{feature.text}</div>
              <div className={styles.includescost}>${feature.cost}</div>
            </div>
          ))}
      </div>
    </div>
  )
}

const EnterprisePackageBuilder = () => {

  const selectedUsers = users => {
    return costData.filter(item => item.users === users)[0]
  }

  const globalState = useContext(store)
  const { state, dispatch } = globalState


  const handleChange = event => {

    const num = parseInt(event.currentTarget.value)
    const userCost = selectedUsers(num)



    for (const feature in userCost.features) {
      const setType = `set${feature.charAt(0).toUpperCase() + feature.slice(1)}`
      let featState = userCost.features[feature].state

      if (feature === 'hosting' && state.features.hosting === true) {
        featState = true
      }

      dispatch({ type: setType, payload: featState })

    }

    dispatch({ type: "setUsers", payload: num })

    dispatch({ type: "setCosting", payload: userCost })
  }

  const features = featuresData

  return (
    <div data-testid="CaEnterpriseAddonCalculator" data-tracking="ca_enterprise_addon_calculator" className={styles.CaEnterpriseAddonCalculator}>
      {/* USERS */}
      <div className={styles.users}>
        {costData.map(item => (
          <div className={styles.radio} key={item.users}>
            <input
              className={styles.radioinput}
              data-testid={`users-${item.users}`}
              id={`users-${item.users}`}
              name="users"
              type="radio"
              value={item.users}
              checked={state.users === item.users}
              onChange={handleChange}
            ></input>
            <div className={styles.radiocircle}></div>
            <label
              className={styles.radiolabel}
              htmlFor={`users-${item.users}`}
            >
              {item.users}
            </label>
          </div>
        ))}
      </div>

      {/* END USERS */}

      <div className={styles.chart}>
        <div className={styles.chartproducts}>
          {features.map(feature => {
            return (
              <EnterprisePackageItem
                key={feature.name}
                name={feature.name}
                title={feature.title}
                cost={state.costing.features[feature.name].cost}
                features={feature.features}
                ctas={feature.ctas}
              />
            )
          })}
        </div>

        <EnterprisePackageTotal base={state.costing.cost} />
      </div>
    </div>
  )
}

export default function CaEnterpriseAddonCalculator() {
  return (
    <>
      <StateProvider>
        <EnterprisePackageBuilder />
      </StateProvider>
    </>
  )
}

