export const countDays = (difference) => Math.floor(difference / (1000 * 60 * 60 * 24))
export const countHours = (difference) => Math.floor((difference / (1000 * 60 * 60)) % 24)
export const countMinutes = (difference) => Math.floor((difference / 1000 / 60) % 60)
export const countSeconds = (difference) => Math.floor((difference / 1000) % 60)