import React from "react";
import { useFetch } from "../utils"
import styles from "./CompareProducts.module.scss";

function CompareProducts({ id }) {
  const iconCheck = (
    <img
      src="https://quickbooks.intuit.com/oidam/intuit/sbseg/en_ca/shared/icons/checkmark-thick.svg"
      className={styles.icon}
      alt="yes"
    />
  );
  const iconCross = (
    <img
      className={styles.icon}
      src="https://quickbooks.intuit.com/oidam/intuit/sbseg/en_us/shared/icons/sbseg-ic_close.svg"
      alt="no"
    />
  );
  const { data, loading } = useFetch(id);

  if (loading) {
    return null
  }
  return (
    <div className={styles.tableComp}>
      <table className={styles.table}>
        <thead className={styles.th}>
          <tr className={styles.tr}>
            <th className={styles.compTitle}>{data.title}</th>
            <th className={styles.prodOne}>{data.productOne}</th>
            <th className={styles.prodTwo}>{data.productTwo}</th>
          </tr>

        </thead>
        <tbody className={styles.tbody}>
          {data.differences.map((compare, index) => {
            return (
              <tr className={styles.tr} key={index}>
                <td
                  className={styles.td}
                  dangerouslySetInnerHTML={{ __html: compare.title }}
                />
                <td className={styles.td}>
                  {compare.prodOne.icon && compare.prodOne.icon === "yes" && iconCheck}
                  {compare.prodOne.icon && compare.prodOne.icon === "no" && iconCross}
                  {compare.prodOne.description && (<span
                    dangerouslySetInnerHTML={{
                      __html: compare.prodOne.description
                    }}
                  />)}
                  {compare.prodOneA && (
                    <>
                      <hr />
                      {compare.prodOneA.icon && compare.prodOneA.icon === "yes" && iconCheck}
                      {compare.prodOneA.icon && compare.prodOneA.icon === "no" && iconCross}
                      {compare.prodOne.description &&
                        <span
                          dangerouslySetInnerHTML={{
                            __html: compare.prodOneA.description
                          }}
                        />}
                    </>
                  )}
                </td>
                <td className={styles.td}>
                  {compare.prodTwo.icon && compare.prodTwo.icon === "yes" && iconCheck}
                  {compare.prodTwo.icon && compare.prodTwo.icon === "no" && iconCross}
                  {compare.prodTwo.description && <span
                    dangerouslySetInnerHTML={{
                      __html: compare.prodTwo.description
                    }}
                  />}
                  {compare.prodTwoA && (
                    <>
                      <hr />
                      {compare.prodTwoA.icon && compare.prodTwoA.icon === "yes" && iconCheck}
                      {compare.prodTwoA.icon && compare.prodTwoA.icon === "no" && iconCross}
                      {compare.prodTwoA.description && <span
                        dangerouslySetInnerHTML={{
                          __html: compare.prodTwoA.description
                        }}
                      />}
                    </>
                  )}
                </td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

export default CompareProducts;
