import { useState, useEffect } from "react";

//Currency Formatter
export const formatCurrency = (amount, locale = "en-CA", decimals = 0) => {
 return new Intl.NumberFormat(locale, {
  style: "currency",
  currency: "CAD",
  minimumFractionDigits: decimals,
  maximumFractionDigits: decimals,
  currencyDisplay: "narrowSymbol"
 }).format(amount);
};

//Fetching data from Pricing.json
export const fetchPricing = (url) => {
 const [data, setData] = useState(null);
 const [loading, setLoading] = useState(true);

 async function getDataFromAPI(url) {
  try {
   const res = await fetch(url);
   const data = await res.json();
   setData(data);
   setLoading(false);
  } catch (e) {
   console.error(e);
  }
 }
 useEffect(() => {
  getDataFromAPI(url);
 }, [url]);

 return { data, loading };
};

//Fetching data from codeblock-contents.json
export const useFetch = (id) => {
 const [data, setData] = useState(null);
 const [loading, setLoading] = useState(true);
 const url = "https://quickbooks.intuit.com/oidam/intuit/sbseg/en_ca/quickbooks-online/web/script-assets/codeblock-contents.json"
 async function getDataFromAPI(url) {
  try {
   const res = await fetch(url);
   const data = await res.json();
   const item = data.filter((check) => check.id === id)[0];
   setData(item);
   setLoading(false);
  } catch (e) {
   console.error(e);
  }
 }
 useEffect(() => {
  getDataFromAPI(url);
 }, [url]);

 return { data, loading };
}

//Checking OutsideClicks
export const useOnClickOutside = (ref, handler) => {
 useEffect(() => {
  const listener = (event) => {
   if (!ref.current || ref.current.contains(event.target)) {
    return;
   }
   handler(event);
  };
  document.addEventListener("mousedown", listener);
  return () => {
   document.removeEventListener("mousedown", listener);
  };
 }, [ref, handler]);
};