import React, { useState } from 'react'
import Button from "../Button"
import { useFetch } from "../utils"
import styles from './CompareProductsWithToggle.module.scss'

export default function CompareProductsWithToggle({ id, defaultNumberOfRowsVisible }) {
  const [numberOfRowsVisible, setNumberOfRowsVisible] = useState(defaultNumberOfRowsVisible)
  const [toggleState, setToggleState] = useState(false)
  const { data, loading } = useFetch(id);
  if (loading) {
    return null
  }
  const iconCheck = (
    <img
      src="https://quickbooks.intuit.com/oidam/intuit/sbseg/en_ca/shared/icons/checkmark-thick.svg"
      className={styles.icon}
      alt="yes"
    />
  );
  const iconCross = (
    <img
      className={styles.icon}
      src="https://quickbooks.intuit.com/oidam/intuit/sbseg/en_us/shared/icons/sbseg-ic_close.svg"
      alt="no"
    />
  );

  const toggleRowsVisible = () => {
    if (!toggleState) {
      setNumberOfRowsVisible(data.differences.length)
      setToggleState(true)
    } else {
      setNumberOfRowsVisible(defaultNumberOfRowsVisible)
      setToggleState(false)
    }
  }
  return (
    <>
      <div className={styles.simpleProductCompare}>
        <div className={styles.shadow__wrapper}>
          <div className={styles.shadow__cell} />
          <div className={styles.shadow__cell} />
          <div className={styles.shadow__cell} />
        </div>
        <div className={styles.headings__wrapper}>
          <div className={styles.headings__row}>
            <div className={styles.headings__cell} dangerouslySetInnerHTML={{ __html: data.title && data.title }} />
            <div className={styles.headings__cell} >
              <span
                className={styles.productTitle}
                dangerouslySetInnerHTML={{ __html: data.productOne }}
              />
              {data.productOneLink && <Button data-wa-link={`buynow-btn-${data.productOneLink.btnText}`} variant="primary" href={data.productOneLink.btnLink}>{data.productOneLink.btnText}</Button>}
            </div>
            <div className={styles.headings__cell} >
              <span
                className={styles.productTitle}
                dangerouslySetInnerHTML={{ __html: data.productTwo }}
              />
              {data.productTwoLink && <Button data-wa-link={`buynow-btn-${data.productTwoLink.btnText}`} variant="primary" href={data.productTwoLink.btnLink}>{data.productTwoLink.btnText}</Button>}
            </div>
          </div>
        </div>
        <div className={styles.shadow__line} />
        <div className={styles.rows__wrapper}>
          {data.differences.slice(0, numberOfRowsVisible).map((compare, index) => (
            <React.Fragment key={index}>
              <div className={styles.rows__row} >
                <div
                  className={styles.rows__cell}
                  dangerouslySetInnerHTML={{ __html: compare.title }}
                />
                <div className={styles.rows__cell}>
                  {compare.prodOne.icon && compare.prodOne.icon === "yes" && iconCheck}
                  {compare.prodOne.icon && compare.prodOne.icon === "no" && iconCross}
                  {compare.prodOne.description && (<span
                    dangerouslySetInnerHTML={{
                      __html: compare.prodOne.description
                    }}
                  />)}
                </div>
                <div className={styles.rows__cell}>
                  {compare.prodTwo.icon && compare.prodTwo.icon === "yes" && iconCheck}
                  {compare.prodTwo.icon && compare.prodTwo.icon === "no" && iconCross}
                  {compare.prodTwo.description && (<span
                    dangerouslySetInnerHTML={{
                      __html: compare.prodTwo.description
                    }}
                  />)}
                </div>
              </div>
            </React.Fragment>
          ))}
        </div>
      </div>
      {/* Buttons */}
      <div className={styles.toggle__button}>
        {!toggleState && (
          <Button data-action="click"
            data-wa-link="qbo-en-ca-button-comparechart-showmore" variant="primary" onClick={toggleRowsVisible}>Show more</Button>
        )}
        {toggleState && (
          <Button data-action="click"
            data-wa-link="qbo-en-ca-button-comparechart-showless" variant="primary" onClick={toggleRowsVisible}>Show less</Button>
        )}
      </div>
    </>

  )
}