import React, { useState, useEffect } from "react";
import styles from './ProductSelectorQuiz.module.scss'
export default function DisplayAnswer({ recommendation }) {
  const [productTitle, setProductTitle] = useState("easystart");
  const [resultImage, setResultImage] = useState("https://quickbooks.intuit.com/oidam/intuit/sa/en_us/quickbooks/illustration/lifestyle/celebrate-01-illo-life-sbseg.svg")
  const [productDescription, setProductDescription] = useState(`It’s a simple but mighty solution that’s perfect for freelancers, independent contractors, and sole proprietors.<br/>With Self Employed, you can organize your receipts, track expenses, record mileage, and get paid — all in one spot. It streamlines bookkeeping tasks so you get more time to focus on the work you love.`)

  const showRecommendedFlag = (flagId) => {
    // document.getElementById('quizpricingcardcontainer').classList.add(styles.showRecommended)
    document.getElementById('quizpricingcardcontainer').style.height = "auto"
    document.getElementById('quizpricingcardcontainer').style.visibility = "visible"

    document.querySelector('[aria-label="Startagain"]').classList.add(styles.showRecommended)
    // document.getElementById('quizpricingcardcontainer').classList.remove("hideRecommended")
    document.querySelector('[aria-label="Startagain"]').classList.remove("hideRecommended")

    const recommendedFlag = document.getElementById(flagId).firstElementChild
    if (recommendedFlag.classList.contains("hideRecommended")) {
      document.getElementById(flagId).firstElementChild.classList.remove("hideRecommended");
      document.getElementById(flagId).firstElementChild.classList.add(styles.showRecommended)
    }
  }


  //Set package
  const doAnswerEasyStart = () => {
    setProductTitle("EasyStart");
    setResultImage("https://quickbooks.intuit.com/oidam/intuit/sa/en_us/quickbooks/illustration/lifestyle/celebrate-01-illo-life-sbseg.svg")
    setProductDescription(`EasyStart is the best of both worlds — it covers all the basics of small business accounting without the extra features you don’t need. With it, you can organize receipts, invoice customers, accept payments, and keep your books tax time ready.`)
    showRecommendedFlag('quizeasystartcard')
  };
  const doAnswerEssentials = () => {
    setProductTitle("Essentials");
    setResultImage("https://quickbooks.intuit.com/oidam/intuit/sa/en_us/quickbooks/illustration/lifestyle/celebrate-01-illo-life-sbseg.svg")
    setProductDescription(`It’s our most popular plan — the one Goldilocks would call "just right."<br/>Essentials has all the core benefits of QuickBooks Online, plus features to meet the growing needs of your business. With it, you can manage bills and suppliers, track employee time, and give access to multiple users.`)
    showRecommendedFlag('quizessentialscard')
  };
  const doAnswerPlus = () => {
    setProductTitle("Plus");
    setResultImage("https://quickbooks.intuit.com/oidam/intuit/sa/en_us/quickbooks/illustration/lifestyle/celebrate-02-illo-life-sbseg.svg")
    setProductDescription(`It’s our most feature-packed plan with all the bells and whistles.<br/>With Plus, you get the core features of QuickBooks Online plus the ability to track profitability by project, and track inventory to keep up with supply and demand.`)
    showRecommendedFlag('quizpluscard')
  };
  const doAnswerSelfEmployed = () => {
    setProductTitle("Self-Employed");
    setResultImage("https://quickbooks.intuit.com/oidam/intuit/sa/en_us/quickbooks/illustration/lifestyle/automation-illo-life-sbseg.svg")
    setProductDescription(`It’s a simple but mighty solution that’s perfect for freelancers, independent contractors, and sole proprietors.<br/>&nbsp;<br/>With Self Employed, you can organize your receipts, track expenses, record mileage, and get paid — all in one spot. It streamlines bookkeeping tasks so you get more time to focus on the work you love.`)
    showRecommendedFlag('quizselfemployedcard')
  };

  useEffect(() => {
    switch (recommendation) {
      case "easystart":
        doAnswerEasyStart();
        break;
      case "essentials":
        doAnswerEssentials();
        break;
      case "plus":
        doAnswerPlus();
        break;
      case "selfemployed":
        doAnswerSelfEmployed();
        break;
      default:
        doAnswerEasyStart();
        break;
    }
  }, [recommendation]);


  return (
    <>
      <div className={styles.productAnswerMessage}>
        <div className={styles.productAnswerImage}>
          <img src={resultImage} alt={productTitle} />
        </div>
        <div className={styles.productAnswerContent}>
          <div className={styles.productAnswerTitle}>Alright!<br />QuickBooks <span className={styles.productAnswerRecommended}>{productTitle}</span> is the one for you.</div>
          <div className={styles.productAnswerText} dangerouslySetInnerHTML={{ __html: productDescription }}></div>
        </div>
      </div>
    </>
  );
}
