import React from 'react'
import styles from './ProductSelectorQuiz.module.scss'

export default function QuizQuestion({ question,
    nextPage,
    prevPage,
    setInputValue,
    state,
    setState }) {

    return (
        <>
            <div className={styles.questionScreen}>
                <div className={styles.questionScreenBackbutton} onClick={(event) => prevPage(event)}>
                    <svg height="20" width="16" xmlns="http://www.w3.org/2000/svg"><path d="M4,10l8-8l-2-2L0,10l10,10l2-2L4,10z"></path></svg>
                </div>
                <div className={styles.questionScreenQuestion} dangerouslySetInnerHTML={{ __html: question.question }}></div>
                <div className={styles.questionScreenSubQuestion} dangerouslySetInnerHTML={{ __html: question.subquestion }}></div>
                <div className={styles.questionScreenOptions}>
                    {
                        question.options.map(option => {
                            return (
                                <div key={`${question.stateSet}-${option.value}`} className={styles.questionScreenOption}
                                    data-wa-link={option.datawalink}
                                    data-action="product-selector-ca"
                                    onClick={(event) => {
                                        setInputValue(question.stateSet, option.value, setState);
                                        nextPage(event);
                                    }}>
                                    <img src={option.image} className={`${styles.optionImage} ${state[question.stateSet] === option.value && styles.optionActive}`} alt="" loading="lazy" />
                                    <div className={styles.optionContents}>
                                        <div className={styles.optionValue} dangerouslySetInnerHTML={{ __html: option.value !== "na" ? option.value : "&nbsp;" }}></div>
                                        <div className={styles.optionText}>{option.title}</div>
                                    </div>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        </>
    )
}