import React, { useState, useEffect } from 'react'
import { countDays, countHours, countMinutes, countSeconds } from './utils'
import styles from "./Countdown.module.scss"

export default function Countdown({ startDate, endDate, gmtoffset, countdownDayLabel, countdownHoursLabel, countdownMinsLabel, countdownSecsLabel }) {
 const [days, setDays] = useState('-')
 const [hours, setHours] = useState('-')
 const [minutes, setMinutes] = useState('-')
 const [seconds, setSeconds] = useState('-')
 const promoStartDate = startDate + gmtoffset
 const promoEndDate = endDate + gmtoffset
 const currDate = new Date(Date.now()).getTime() + gmtoffset
 const difference = promoEndDate - currDate
 useEffect(() => {
  if (startDate && endDate && currDate >= promoStartDate && difference > 0) {
   const tick = setInterval(() => {
    setDays(countDays(difference))
    setHours(countHours(difference))
    setMinutes(countMinutes(difference))
    setSeconds(countSeconds(difference))
   }, 1000)
   return () => clearInterval(tick)
  }
 }, [seconds])
 if (difference < 0) {
  return null
 }
 return (
  <>
   <div className={styles.countdown}>
    <div className={styles.unit}>
     <div className={styles.number}>{days}</div>
     <div className={styles.length}>{countdownDayLabel}</div>
    </div>
    <div className={styles.unit}>
     <div className={styles.number}>{hours}</div>
     <div className={styles.length}>{countdownHoursLabel}</div>
    </div>
    <div className={styles.unit}>
     <div className={styles.number}>{minutes}</div>
     <div className={styles.length}>{countdownMinsLabel}</div>
    </div>
    <div className={styles.unit}>
     <div className={styles.number}>
      {seconds}
     </div>
     <div className={styles.length}>{countdownSecsLabel}</div>
    </div>
   </div>
  </>
 )
}