import React, { useState, useRef } from "react"
import styles from "./Tooltip.module.scss"
import { useOnClickOutside } from "../utils"

export default function Tooltip({ featuretitle, tooltip, placement, link }) {
 const [visible, setVisible] = useState(false)
 const toolRef = useRef()
 function handleClick() {
  setVisible(!visible)
 }

 useOnClickOutside(toolRef, () => {
  setVisible(false);
 });

 const place = placement ? placement : 'top'

 return (
  <div ref={toolRef} className={styles.tooltip}>
   <button className={`${styles.tooltipBtn} ${link ? styles.after : styles.before}`} onClick={() => { handleClick() }}>{featuretitle}</button>
   {visible && (
    <div className={`${styles.tooltipConent} ${styles[place]}`}>
     <span className={styles.arrow}></span>
     <div dangerouslySetInnerHTML={{ __html: tooltip }} />
    </div>
   )}
  </div>
 )
}