import React, { useState, useEffect } from "react";
import ReactDom from "react-dom";
import styles from "./Modal.module.scss";
import Button from '../Button'

import Countdown from "../Countdown/Countdown"

export default function Modal({ auto, open, children, onClose, promoContents, modalId }) {
 const [show, setShow] = useState(open)
 useEffect(() => {
  window.openModal = () => setShow(true)

  return () => window.openModal = null
 }, [])

 useEffect(() => {
  if (auto && !open) {

   const timer = setTimeout(() => {

    setShow(true)
   }, 500);
   return () => clearTimeout(timer);
  }

 }, []);

 //TEST
 useEffect(() => {
  if (open) return

  const addCaptureModalTrigger = (event) => {
   document.querySelectorAll('[data-targetmodalid]').forEach((button) => {
    if (button.getAttribute('data-targetmodalid') === modalId) {
     button.addEventListener('click', () => setShow(true))
    }
   })
  }

  // Add click event listeners on document.readyState=='interactive'
  addCaptureModalTrigger()
  // Add click event listeners on document.readyState=='complete'
  window.addEventListener('load', addCaptureModalTrigger)
 }, [])
 //TEST
 // Countdown
 const startDate = new Date(`${promoContents.countdownStartDate}T00:00:00.000-04:00`).getTime(); //countdownStart:'2021-07-13T00:00:00.000-04:00',
 const endDate = new Date(`${promoContents.countdownEndDate}T00:00:00.000-04:00`).getTime();
 const gmtoffset = -25200000;
 //Countdown

 useEffect(() => {
  const handleClick = (e) => {
   if (e.target && e.target.className === "modal") {
    return setShow(false);
   }
  };
  if (show) {
   window.addEventListener("click", handleClick);
  }
 }, [show]);


 if (!show) return null;


 const closeModal = onClose ? onclose : () => setShow(false)

 const promo = (

  <div className={styles.promoContent}>
   {promoContents.subtitle && <div className={styles.promoSubtitle} dangerouslySetInnerHTML={{ __html: promoContents.subtitle }}></div>}
   {promoContents.title && <div className={styles.promoTitle} dangerouslySetInnerHTML={{ __html: promoContents.title }}></div>}
   {promoContents.countdownStartDate && <Countdown startDate={startDate} endDate={endDate} gmtoffset={gmtoffset} countdownDayLabel={promoContents.countdownDayLabel} countdownHoursLabel={promoContents.countdownHoursLabel} countdownMinsLabel={promoContents.countdownMinsLabel} countdownSecsLabel={promoContents.countdownSecsLabel} />}
   {promoContents.btnText && <Button data-wa-link={`promo-btn`} variant={promoContents.btnTheme} href={promoContents.btnLink}>{promoContents.btnText}</Button>}
  </div>

 )

 return ReactDom.createPortal(
  <div data-modalid={modalId}>
   <div className={styles.modal} onClick={closeModal} />
   <div className={`${styles.modalContent} ${styles.teal}`}>
    <div className={styles.closeButton} onClick={closeModal}>
     <svg
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      fill="none"
      viewBox="0 0 24 24"
     >
      <path
       fill="#6B6C72"
       d="M13.432 11.984l5.3-5.285a1 1 0 10-1.412-1.416l-5.3 5.285-5.285-5.3a1 1 0 10-1.416 1.411l5.285 5.3L5.3 17.265a1 1 0 101.412 1.416l5.3-5.285L17.3 18.7a1 1 0 101.416-1.412l-5.284-5.304z"
      />
     </svg>
    </div>
    {children ? children : promo}
   </div>
  </div>,
  document.body
 );
}
