import React from "react"
import Button from '../Button'
import Tooltip from "../Tooltip"
import styles from "./QuickbooksPayrollChart.module.scss"
import { useFetch, useOnClickOutside, formatCurrency } from "../utils"




export default function QuickbooksPayrollChart({ id }) {
  const { data, loading } = useFetch(id);
  if (!loading) {

    return (
      <div data-testid="CaQboPayrollCard" data-tracking="ca_qbo_payroll_card" className={styles.quickbookspayrollchart}>
        <div className={styles.pricingcolumn}>
          <div className={styles.head}>
            <div className={styles.headtitle}>{data.title}</div>
            <div className={styles.headpricing}>{formatCurrency(data.price, data.lang)}<small> + </small>{formatCurrency(data.price, data.lang)}<small>/{data.employee}</small></div>
            <div className={styles.headduration}>{data.duration}</div>
            <div className={styles.headcta}>

              <Button data-action="click"
                data-wa-link="qbo-en-ca-button-tryitfree"
                variant="primary"
                href={data.link}>{data.linkText}</Button>
            </div>
          </div>
          <ul className={styles.columnmainlist}>
            <p>Includes:</p>
            {data.features.map((feature, index) => {
              return (
                <li className={styles.mainfeature} key={index}>
                  <Tooltip featuretitle={feature.title} tooltip={feature.tooltip} />
                </li>
              )
            })}
          </ul>
        </div>
      </div>
    )
  }
  return null

}

