import React from 'react';
import { render } from 'react-dom';
//Components
import { Portal, Components } from "@codeblocks/components";
import { fixJson } from './utils'
import "./styles.scss"


const componentArray = [];

document.querySelectorAll("[data-component]").forEach((item, index) => {
  const name = item.dataset["component"];
  const props = item.dataset["props"]
  const compId = `ca-codeblocks-${name}-${index}`;
  item.id = compId;
  item.classList.add(`ca-codeblocks-${name}-component`)
  const Component = Components[name];
  componentArray.push({ Component: Component, id: compId, props: props });
});


render(
  <>
    {
      componentArray.map(({ Component, id, props }, index) => {
        let updatedProps = {}
        if (props) {
          updatedProps = fixJson(props)
          updatedProps = JSON.parse(updatedProps)
        }
        return (
          <Portal key={index} container={id}>
            <Component {...updatedProps} />
          </Portal>
        );
      })

    }
  </>,
  document.getElementById("code-block")
);

